<template>
  <view class="profile-page">
    <!-- ÂÜÖÂÆπÂå∫Âüü -->
    <view class="content">
      <!-- Áî®Êà∑‰ø°ÊÅØÂç°Áâá -->
      <view class="user-info-card">
        <image 
          class="user-avatar-large" 
          :src="userInfo.displayAvatar || userInfo.avatarUrl || '/static/zhuye/lanmei_boy.png'" 
          mode="aspectFill"
        />
        <view class="user-info-text">
          <text class="user-name">{{ userInfo.displayName || userInfo.nickName || 'Áî®Êà∑' }}</text>
          <text class="user-days" v-if="isBound && bindTime">Âú®ÊÅãÁà±‰∏≠ {{ daysTogether }} Â§©</text>
          <text class="user-days" v-else>Á≠âÂæÖÂè¶‰∏ÄÂçä</text>
        </view>
        <view class="edit-icon" @click="goToEdit">
          <text>‚úèÔ∏è</text>
        </view>
      </view>

      <!-- ÊàëÁöÑÊàêÂ∞± -->
      <view class="section achievements-section">
        <text class="section-title">ÊàëÁöÑÊàêÂ∞±</text>
        <view class="achievements-grid">
          <view class="achievement-item" v-for="(achievement, index) in achievements" :key="index">
            <view class="achievement-icon" :style="{ background: achievement.bgColor }">
              <text class="achievement-emoji">{{ achievement.icon }}</text>
            </view>
            <text class="achievement-name">{{ achievement.name }}</text>
          </view>
        </view>
      </view>

      <!-- ËÆæÁΩÆ -->
      <view class="section settings-section">
        <text class="section-title">ËÆæÁΩÆ</text>
        <view class="settings-list">
          <view class="setting-item" @click="handleSetting('notification')">
            <view class="setting-left">
              <text class="setting-icon">üîî</text>
              <text class="setting-text">ÈÄöÁü•ËÆæÁΩÆ</text>
            </view>
            <text class="setting-arrow">‚Ä∫</text>
          </view>
          <view class="setting-item" @click="handleSetting('privacy')">
            <view class="setting-left">
              <text class="setting-icon">üõ°Ô∏è</text>
              <text class="setting-text">ÈöêÁßÅËÆæÁΩÆ</text>
            </view>
            <text class="setting-arrow">‚Ä∫</text>
          </view>
          <view class="setting-item" @click="handleSetting('sync')">
            <view class="setting-left">
              <text class="setting-icon">‚òÅÔ∏è</text>
              <text class="setting-text">‰∫ëÂêåÊ≠•</text>
            </view>
            <text class="setting-arrow">‚Ä∫</text>
          </view>
        </view>
      </view>

      <!-- Ë¥¶Âè∑‰∏éÂÆâÂÖ® -->
      <view class="section account-section">
        <view class="setting-item" @click="handleSetting('account')">
          <view class="setting-left">
            <text class="setting-icon">üîí</text>
            <text class="setting-text">Ë¥¶Âè∑‰∏éÂÆâÂÖ®</text>
          </view>
          <text class="setting-arrow">‚Ä∫</text>
        </view>
      </view>

      <!-- Â§¥ÂÉèËÆæÁΩÆ -->
      <view class="section">
        <text class="section-title">Â§¥ÂÉèËÆæÁΩÆ</text>
        <view class="avatar-section">
          <view class="current-avatar">
            <image class="avatar" :src="userInfo.displayAvatar || userInfo.avatarUrl" mode="aspectFill" />
            <text class="avatar-label">ÂΩìÂâçÂ§¥ÂÉè</text>
          </view>
          
          <view class="avatar-options">
            <button class="avatar-btn" @click="selectWechatAvatar">
              <text class="btn-icon">üì±</text>
              <text class="btn-text">‰ΩøÁî®ÂæÆ‰ø°Â§¥ÂÉè</text>
            </button>
            
            <button class="avatar-btn" @click="uploadCustomAvatar">
              <text class="btn-icon">üñºÔ∏è</text>
              <text class="btn-text">‰∏ä‰º†Ëá™ÂÆö‰πâÂ§¥ÂÉè</text>
            </button>
          </view>
        </view>
      </view>

      <!-- ÊòµÁß∞ËÆæÁΩÆ -->
      <view class="section">
        <text class="section-title">ÊòµÁß∞ËÆæÁΩÆ</text>
        <view class="nickname-section">
          <view class="nickname-option" @click="toggleUseWechatNickname">
            <view class="checkbox" :class="{ checked: useWechatNickname }"></view>
            <text class="option-text">‰ΩøÁî®ÂæÆ‰ø°ÊòµÁß∞</text>
            <text class="current-nickname">{{ userInfo.nickName }}</text>
          </view>
          
          <view v-if="!useWechatNickname" class="custom-nickname">
            <input 
              v-model="customNickname" 
              class="nickname-input" 
              placeholder="ËØ∑ËæìÂÖ•Ëá™ÂÆö‰πâÊòµÁß∞"
              maxlength="20"
            />
            <text class="char-count">{{ customNickname.length }}/20</text>
          </view>
        </view>
      </view>

      <!-- ‰øùÂ≠òÊåâÈíÆ -->
      <view class="save-section">
        <button class="save-btn" @click="saveProfile">
          <text class="save-icon">üíæ</text>
          <text class="save-text">‰øùÂ≠òËÆæÁΩÆ</text>
        </button>
      </view>
    </view>
  </view>
</template>

<script>
import http from '@/utils/http.js';
import config from '@/utils/config.js';
import { getCoupleInfo, getPartnerInfo, isBound as checkIsBound } from '../../utils/couple.js';
import { getCoupleStatus } from '../../api/couple.js';
import { saveCoupleInfo } from '../../utils/couple.js';

export default {
  data() {
    return {
      userInfo: {
        nickName: '',
        avatarUrl: '',
        displayName: '',
        displayAvatar: ''
      },
      useWechatNickname: true,
      customNickname: '',
      isLoading: false,
      // ÊÉÖ‰æ£ÂÖ≥Á≥ªÁõ∏ÂÖ≥
      isBound: false,
      partnerInfo: null,
      bindTime: '',
      // ÊàêÂ∞±Êï∞ÊçÆ
      achievements: [
        { icon: 'üßÅ', name: 'ÁæéÈ£üÂÆ∂', bgColor: 'rgba(255, 217, 61, 0.2)' },
        { icon: '‚úàÔ∏è', name: 'ÊóÖË°åËÄÖ', bgColor: 'rgba(255, 158, 188, 0.2)' },
        { icon: 'üìÖ', name: 'Á∫™ÂøµÊó•', bgColor: 'rgba(217, 172, 255, 0.2)' }
      ]
    };
  },
  computed: {
    // ËÆ°ÁÆóÂú®‰∏ÄËµ∑ÁöÑÂ§©Êï∞
    daysTogether() {
      if (!this.bindTime) return 0;
      try {
        const bindDate = new Date(this.bindTime);
        const now = new Date();
        const diffTime = now - bindDate;
        const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
        return diffDays > 0 ? diffDays : 1; // Ëá≥Â∞ëÊòæÁ§∫1Â§©
      } catch (e) {
        return 0;
      }
    }
  },
  
  onLoad() {
    this.loadUserInfo();
    this.loadCoupleInfo();
  },
  onShow() {
    // ÊØèÊ¨°È°µÈù¢ÊòæÁ§∫Êó∂ÈáçÊñ∞Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØÂíåÊÉÖ‰æ£‰ø°ÊÅØ
    this.loadUserInfo();
    this.loadCoupleInfo();
  },
  
  methods: {
    // Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
    loadUserInfo() {
      try {
        const loginInfo = uni.getStorageSync('login_info');
        if (loginInfo && loginInfo.userInfo) {
          this.userInfo = { ...loginInfo.userInfo };
          this.useWechatNickname = !this.userInfo.displayName || 
            this.userInfo.displayName === this.userInfo.nickName;
          this.customNickname = this.useWechatNickname ? '' : this.userInfo.displayName;
        }
      } catch (error) {
        console.error('Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•', error);
      }
    },
    
    // ËøîÂõû‰∏ä‰∏ÄÈ°µÔºà‰ΩøÁî®Á≥ªÁªüÂØºËà™Ê†èÁöÑËøîÂõûÊåâÈíÆÔºâ
    
    // ‰ΩøÁî®ÂæÆ‰ø°Â§¥ÂÉè
    selectWechatAvatar() {
      this.userInfo.displayAvatar = this.userInfo.avatarUrl;
      uni.showToast({
        title: 'Â∑≤ÂàáÊç¢‰∏∫ÂæÆ‰ø°Â§¥ÂÉè',
        icon: 'success',
        duration: 1500
      });
    },
    
    // ‰∏ä‰º†Ëá™ÂÆö‰πâÂ§¥ÂÉè
    uploadCustomAvatar() {
      uni.chooseImage({
        count: 1,
        sizeType: ['compressed'],
        sourceType: ['album', 'camera'],
        success: async (res) => {
          const tempFilePath = res.tempFilePaths[0];
          
          try {
            uni.showLoading({
              title: 'Â§ÑÁêÜÂ§¥ÂÉè‰∏≠...',
              mask: true
            });
            
            // ÂéãÁº©ÂõæÁâá
            const compressedImage = await this.compressImage(tempFilePath);
            
            // Â∞ùËØï‰∏ä‰º†Âà∞ÊúçÂä°Âô®
            try {
              const uploadResult = await http.upload({
                url: config.API.USER.AVATAR_UPLOAD,
                filePath: compressedImage,
                name: 'avatar',
                formData: { type: 'avatar' }
              });
              this.userInfo.displayAvatar = uploadResult.url || tempFilePath;
            } catch (uploadError) {
              // ‰∏ä‰º†Â§±Ë¥•Ôºå‰ΩøÁî®Êú¨Âú∞ÂõæÁâá
              console.warn('Â§¥ÂÉè‰∏ä‰º†Â§±Ë¥•Ôºå‰ΩøÁî®Êú¨Âú∞ÂõæÁâá', uploadError);
              this.userInfo.displayAvatar = tempFilePath;
            }
            
            uni.showToast({
              title: 'Â§¥ÂÉè‰∏ä‰º†ÊàêÂäü',
              icon: 'success',
              duration: 1500
            });
            
          } catch (error) {
            console.error('Â§ÑÁêÜÂ§¥ÂÉèÂ§±Ë¥•', error);
            uni.showToast({
              title: 'Â§¥ÂÉèÂ§ÑÁêÜÂ§±Ë¥•',
              icon: 'none'
            });
          } finally {
            uni.hideLoading();
          }
        },
        fail: (err) => {
          console.error('ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•', err);
        }
      });
    },
    
    // ÂéãÁº©ÂõæÁâá
    compressImage(tempFilePath) {
      return new Promise((resolve, reject) => {
        uni.compressImage({
          src: tempFilePath,
          quality: 80,
          success: (res) => {
            resolve(res.tempFilePath);
          },
          fail: (error) => {
            console.warn('ÂõæÁâáÂéãÁº©Â§±Ë¥•Ôºå‰ΩøÁî®ÂéüÂõæ', error);
            resolve(tempFilePath);
          }
        });
      });
    },
    
    // ÂàáÊç¢ÊòØÂê¶‰ΩøÁî®ÂæÆ‰ø°ÊòµÁß∞
    toggleUseWechatNickname() {
      this.useWechatNickname = !this.useWechatNickname;
      if (this.useWechatNickname) {
        this.customNickname = '';
      }
    },
    
    // ‰øùÂ≠ò‰∏™‰∫∫ËµÑÊñô
    saveProfile() {
      // È™åËØÅËá™ÂÆö‰πâÊòµÁß∞
      if (!this.useWechatNickname && !this.customNickname.trim()) {
        uni.showToast({
          title: 'ËØ∑ËæìÂÖ•Ëá™ÂÆö‰πâÊòµÁß∞',
          icon: 'none'
        });
        return;
      }
      
      this.isLoading = true;
      
      try {
        // ÊûÑÂª∫ÊúÄÁªàÁî®Êà∑‰ø°ÊÅØ
        const displayName = this.useWechatNickname 
          ? this.userInfo.nickName 
          : this.customNickname.trim();
          
        const displayAvatar = this.userInfo.displayAvatar || this.userInfo.avatarUrl;
        
        // Êõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®
        const loginInfo = uni.getStorageSync('login_info') || {};
        loginInfo.userInfo = {
          ...loginInfo.userInfo,
          displayName,
          displayAvatar,
          originalNickName: this.userInfo.nickName,
          originalAvatarUrl: this.userInfo.avatarUrl
        };
        
        uni.setStorageSync('login_info', loginInfo);
        
        uni.showToast({
          title: '‰øùÂ≠òÊàêÂäü',
          icon: 'success',
          duration: 1500
        });
        
        // Âª∂ËøüËøîÂõû
        setTimeout(() => {
          uni.navigateBack();
        }, 1500);
        
      } catch (error) {
        console.error('‰øùÂ≠òÂ§±Ë¥•', error);
        uni.showToast({
          title: '‰øùÂ≠òÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        });
      } finally {
        this.isLoading = false;
      }
    },
    // Âä†ËΩΩÊÉÖ‰æ£‰ø°ÊÅØ
    async loadCoupleInfo() {
      try {
        // ÂÖàÊ£ÄÊü•Êú¨Âú∞
        const localCoupleInfo = getCoupleInfo();
        if (localCoupleInfo && localCoupleInfo.isBound) {
          this.isBound = true;
          this.partnerInfo = localCoupleInfo.partnerInfo || null;
          this.bindTime = localCoupleInfo.bindTime || '';
          
          // ÂêåÊó∂‰ªéÊúçÂä°Âô®ÂêåÊ≠•‰∏ÄÊ¨°Áä∂ÊÄÅ
          try {
            const response = await getCoupleStatus();
            if (response && response.data && response.data.isBound) {
              // Êõ¥Êñ∞Êú¨Âú∞‰ø°ÊÅØ
              saveCoupleInfo({
                isBound: true,
                coupleId: response.data.coupleId,
                partnerId: response.data.partnerInfo?.userId || '',
                partnerInfo: response.data.partnerInfo || {},
                bindTime: response.data.bindTime || '',
                role: response.data.role || ''
              });
              this.partnerInfo = response.data.partnerInfo || {};
              this.bindTime = response.data.bindTime || '';
            } else {
              // ÊúçÂä°Âô®ËøîÂõûÊú™ÁªëÂÆöÔºåÊ∏ÖÈô§Êú¨Âú∞
              this.isBound = false;
              this.partnerInfo = null;
              this.bindTime = '';
            }
          } catch (e) {
            console.error('ÂêåÊ≠•ÁªëÂÆöÁä∂ÊÄÅÂ§±Ë¥•', e);
            // ÂêåÊ≠•Â§±Ë¥•Êó∂‰øùÊåÅÊú¨Âú∞Áä∂ÊÄÅ
          }
          return;
        }
        
        // Êú¨Âú∞Ê≤°ÊúâÔºåÊü•ËØ¢ÊúçÂä°Âô®
        this.isBound = false;
        this.partnerInfo = null;
        try {
          const response = await getCoupleStatus();
          if (response && response.data && response.data.isBound) {
            this.isBound = true;
            this.partnerInfo = response.data.partnerInfo || {};
            this.bindTime = response.data.bindTime || '';
            
            // ‰øùÂ≠òÂà∞Êú¨Âú∞
            saveCoupleInfo({
              isBound: true,
              coupleId: response.data.coupleId,
              partnerId: response.data.partnerInfo?.userId || '',
              partnerInfo: response.data.partnerInfo || {},
              bindTime: response.data.bindTime || '',
              role: response.data.role || ''
            });
          }
        } catch (e) {
          console.error('Êü•ËØ¢ÁªëÂÆöÁä∂ÊÄÅÂ§±Ë¥•', e);
          // Êü•ËØ¢Â§±Ë¥•Êó∂‰ΩøÁî®Êú¨Âú∞Áä∂ÊÄÅ
          this.isBound = checkIsBound();
          if (this.isBound) {
            this.partnerInfo = getPartnerInfo();
            const coupleInfo = getCoupleInfo();
            this.bindTime = coupleInfo ? coupleInfo.bindTime : '';
          }
        }
      } catch (e) {
        console.error('Âä†ËΩΩÊÉÖ‰æ£‰ø°ÊÅØÂ§±Ë¥•', e);
        this.isBound = checkIsBound();
        if (this.isBound) {
          this.partnerInfo = getPartnerInfo();
        }
      }
    },
    // Ë∑≥ËΩ¨Âà∞ÁºñËæëÈ°µÈù¢ÔºàÂ±ïÂºÄÊòæÁ§∫ÁºñËæëÈÄâÈ°πÔºâ
    goToEdit() {
      // ÊªöÂä®Âà∞Â§¥ÂÉèËÆæÁΩÆÂå∫Âüü
      uni.pageScrollTo({
        selector: '.avatar-section',
        duration: 300
      });
    },
    // Â§ÑÁêÜËÆæÁΩÆÈ°πÁÇπÂáª
    handleSetting(key) {
      const settingMap = {
        notification: 'ÈÄöÁü•ËÆæÁΩÆ',
        privacy: 'ÈöêÁßÅËÆæÁΩÆ',
        sync: '‰∫ëÂêåÊ≠•',
        account: 'Ë¥¶Âè∑‰∏éÂÆâÂÖ®'
      };
      uni.showToast({
        title: settingMap[key] + 'ÔºàÂæÖÂºÄÂèëÔºâ',
        icon: 'none'
      });
    }
  }
};
</script>

